form label {
  display: block;
  font-weight: bold;
  color: #444444;
}

form input {
  width: 100%;
}

form button {
  color: #444444;
  background: #f3f3f3;
  border: 1px #dadada solid;
  padding: 5px 10px;
  border-radius: 2px;
  font-weight: bold;
  font-size: 9pt;
  outline: none;
  margin-top: 5px;
}

form button.link {
  font-size: 80%;
  font-weight: bold;
  margin-right: 3px;
  margin-bottom: 5px;
  text-decoration: none;
  background: none;
  border: none;
  padding: 2px;
}

form button.link:hover {
  cursor: pointer;
}

.panel {
  padding: 2px;
  margin: 2px;
  background: #eeeeee;
  border: thin solid #cccccc;
}
